@mixin border-box {
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    & *{ box-sizing: border-box; }
    &:before{ box-sizing: border-box; }
    &:after{ box-sizing: border-box; }
    & *:before{ box-sizing: border-box; }
    & *:after{ box-sizing: border-box; }
}

// Center block
@mixin center-block {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

/* Container */
@mixin container-size($size){
    @if $size == "wide" {
        max-width: $container-max-width;
        @include center-block();
    } @else {
        max-width: $container-fluid;
    }
    
    
}

@mixin colors-tx-bg($bg-color, $tx-color) {
    background-color: $bg-color;
    color: $tx-color;
}

/*--------------------------
    Margin & Padding Mixing
---------------------------*/


@mixin margin($number, $sizes){
    @if type-of($number) != "number" {
		@error "`#{$number}` is not a valid value for $values. It must be a number.";
	}
    @for $i from 1 through $number {
        .mt-#{$i}{ margin-top: #{$i}rem; }
        .mb-#{$i}{ margin-bottom: #{$i}rem; }
        .ml-#{$i}{ margin-left: #{$i}rem; }
        .mr-#{$i}{ margin-right: #{$i}rem; }
        .mx-#{$i}{ margin-left: #{$i}rem; margin-right: #{$i}rem; }
        .my-#{$i}{ margin-top: #{$i}rem; margin-bottom: #{$i}rem; }
        .m-#{$i}{ margin: #{$i}rem; }

        @each $label, $size in $sizes{
            @include mq($until: $size){
                .mt-#{$i}-#{$label}{ padding-top: #{$i}rem; }
                .mb-#{$i}-#{$label}{ padding-bottom: #{$i}rem; }
                .ml-#{$i}-#{$label}{ padding-left: #{$i}rem; }
                .mr-#{$i}-#{$label}{ padding-right: #{$i}rem; }
                .mx-#{$i}-#{$label}{ padding-left: #{$i}rem; padding-right: #{$i}rem; }
                .my-#{$i}-#{$label}{ padding-top: #{$i}rem; padding-bottom: #{$i}rem; }
                .m-#{$i}-#{$label}{ padding: #{$i}rem; }
            }
            
        }

    }
}

@mixin padding($number, $sizes){
    @if type-of($number) != "number" {
		@error "`#{$number}` is not a valid value for $values. It must be a number.";
	}
    @for $i from 1 through $number {
        .pt-#{$i}{ padding-top: #{$i}rem; }
        .pb-#{$i}{ padding-bottom: #{$i}rem; }
        .pl-#{$i}{ padding-left: #{$i}rem; }
        .pr-#{$i}{ padding-right: #{$i}rem; }
        .px-#{$i}{ padding-left: #{$i}rem; padding-right: #{$i}rem; }
        .py-#{$i}{ padding-top: #{$i}rem; padding-bottom: #{$i}rem; }
        .p-#{$i}{ padding: #{$i}rem; }

        @each $label, $size in $sizes{
            @include mq($until: $size){
                .pt-#{$i}-#{$label}{ padding-top: #{$i}rem; }
                .pb-#{$i}-#{$label}{ padding-bottom: #{$i}rem; }
                .pl-#{$i}-#{$label}{ padding-left: #{$i}rem; }
                .pr-#{$i}-#{$label}{ padding-right: #{$i}rem; }
                .px-#{$i}-#{$label}{ padding-left: #{$i}rem; padding-right: #{$i}rem; }
                .py-#{$i}-#{$label}{ padding-top: #{$i}rem; padding-bottom: #{$i}rem; }
                .p-#{$i}-#{$label}{ padding: #{$i}rem; }
            }
            
        }
    }
}

/*---------------------
    Positioning Mixing
----------------------*/
@mixin position($start, $end, $sizes){
    @if type-of($start) != "number" or type-of($end) != "number"{
		@error "not a valid value for $values. It must be a number.";
	}
    @for $i from $start through $end {
        .top-#{$i}-base { top: #{$i}rem; }
        .bottom-#{$i}-base{ bottom: #{$i}rem; }
        .left-#{$i}-base{ left: #{$i}rem; }
        .right-#{$i}-base{ right: #{$i}rem; }

        @each $label, $size in $sizes{
            @include mq($until: $size){
                .top-#{$i}-#{$label}{ top: #{$i}rem; }
                .bottom-#{$i}-#{$label}{ bottom: #{$i}rem; }
                .left-#{$i}-#{$label}{ left: #{$i}rem; }
                .right-#{$i}-#{$label}{ right: #{$i}rem; }
            }
            
        }
    }
}

/*--------------
    Flex Mixing
---------------*/

@mixin flex-order($number){
    @if type-of($number) != "number" {
		@error "`#{$number}` is not a valid value for $values. It must be a number.";
	}
    @for $i from 1 through $number {
        .flex-order-#{$i}{ order: #{$i}; }
    }
}

//da inserire dentro la class d-flex .... forse non  la soluzione migliore
@mixin col-size-responsive($el){
    @each $label, $size in $mq-breakpoints {
        #{$el}.col-#{$label}-responsive{
            @include mq($until: $size){
                flex-direction: column;
            }
        }
        #{$el}.col-#{$label}-responsive-reverse{
            @include mq($until: $size){
                flex-direction: column-reverse;

            }
        }
    }
}

@mixin flex-basis-steps($step, $sizes){
    @for $i from 1 to ceil(100 / $step)+2{
        $value: ($i - 1)*$step;
        .flex-basis-#{$value}{ flex-basis: percentage($value / 100); }
        @each $label, $size in $sizes{
            .flex-basis-#{$label}-#{$value}{ 
                @include mq($until: $size){
                    flex-basis: percentage($value / 100); 
                }
            }
        }
    }
}